'use client';

import { useEffect, useState } from 'react';
import { getBusinessConfig, BusinessConfig } from '@/lib/business-config';
import { Inter } from 'next/font/google';
import '../globals.css';

const inter = Inter({ subsets: ['latin'] });

interface BusinessLayoutProps {
  children: React.ReactNode;
  params: { businessId: string };
}

export default function BusinessLayout({ children, params }: BusinessLayoutProps) {
  const [businessConfig, setBusinessConfig] = useState<BusinessConfig | null>(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  useEffect(() => {
    const config = getBusinessConfig(params.businessId);
    if (config) {
      setBusinessConfig(config);
      
      // Apply dynamic theming
      const root = document.documentElement;
      root.style.setProperty('--primary-color', config.primaryColor);
      root.style.setProperty('--secondary-color', config.secondaryColor);
      
      // Update page title
      document.title = `${config.name} - Order Management`;
    } else {
      setError('Business not found');
    }
    setLoading(false);
  }, [params.businessId]);

  if (loading) {
    return (
      <html lang="en">
        <body className={inter.className}>
          <div className="min-h-screen bg-gray-50 flex items-center justify-center">
            <div className="text-center">
              <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-blue-600 mx-auto mb-4"></div>
              <p className="text-gray-600">Loading business configuration...</p>
            </div>
          </div>
        </body>
      </html>
    );
  }

  if (error || !businessConfig) {
    return (
      <html lang="en">
        <body className={inter.className}>
          <div className="min-h-screen bg-gray-50 flex items-center justify-center">
            <div className="text-center max-w-md mx-auto p-6">
              <div className="bg-red-100 border border-red-400 text-red-700 px-4 py-3 rounded mb-4">
                <h2 className="font-bold text-lg mb-2">Business Not Found</h2>
                <p>The business ID "{params.businessId}" could not be found.</p>
              </div>
              <div className="text-sm text-gray-600">
                <p className="mb-2">Available demo businesses:</p>
                <ul className="list-disc list-inside space-y-1">
                  <li><code>demo-store</code> - Demo Electronics Store</li>
                  <li><code>fashion-boutique</code> - Elegant Fashion Boutique</li>
                </ul>
              </div>
            </div>
          </div>
        </body>
      </html>
    );
  }

  return (
    <html lang="en">
      <body className={inter.className}>
        <div className="min-h-screen bg-gray-50">
          {/* Business Header */}
          <header className="bg-white shadow-sm border-b">
            <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
              <div className="flex justify-between items-center h-16">
                {/* Business Logo and Name */}
                <div className="flex items-center space-x-4">
                  {businessConfig.logo && (
                    <img
                      src={businessConfig.logo}
                      alt={`${businessConfig.name} logo`}
                      className="h-10 w-auto"
                      onError={(e) => {
                        e.currentTarget.style.display = 'none';
                      }}
                    />
                  )}
                  <div>
                    <h1 className="text-xl font-bold text-gray-900">
                      {businessConfig.name}
                    </h1>
                    <p className="text-sm text-gray-500">Order Management Dashboard</p>
                  </div>
                </div>

                {/* Navigation */}
                <nav className="hidden md:flex space-x-8">
                  <a
                    href={`/${params.businessId}/dashboard`}
                    className="text-gray-700 hover:text-blue-600 px-3 py-2 rounded-md text-sm font-medium transition-colors"
                  >
                    Dashboard
                  </a>
                  <a
                    href={`/${params.businessId}/analytics`}
                    className="text-gray-700 hover:text-blue-600 px-3 py-2 rounded-md text-sm font-medium transition-colors"
                  >
                    Analytics
                  </a>
                  <a
                    href={`/${params.businessId}/settings`}
                    className="text-gray-700 hover:text-blue-600 px-3 py-2 rounded-md text-sm font-medium transition-colors"
                  >
                    Settings
                  </a>
                </nav>

                {/* Subscription Info */}
                <div className="flex items-center space-x-4">
                  <div className="hidden sm:block text-right">
                    <p className="text-sm font-medium text-gray-900 capitalize">
                      {businessConfig.subscription.plan} Plan
                    </p>
                    <p className="text-xs text-gray-500">
                      {businessConfig.subscription.currentUsage.orders}/{businessConfig.subscription.ordersLimit} orders
                    </p>
                  </div>
                  <div className="flex items-center space-x-2">
                    <div className="w-8 h-8 bg-gradient-to-r from-blue-500 to-purple-600 rounded-full flex items-center justify-center">
                      <span className="text-white text-sm font-bold">
                        {businessConfig.name.charAt(0).toUpperCase()}
                      </span>
                    </div>
                  </div>
                </div>
              </div>
            </div>

            {/* Mobile Navigation */}
            <div className="md:hidden border-t border-gray-200">
              <div className="px-2 pt-2 pb-3 space-y-1">
                <a
                  href={`/${params.businessId}/dashboard`}
                  className="block px-3 py-2 text-base font-medium text-gray-700 hover:text-blue-600 hover:bg-gray-50 rounded-md transition-colors"
                >
                  Dashboard
                </a>
                <a
                  href={`/${params.businessId}/analytics`}
                  className="block px-3 py-2 text-base font-medium text-gray-700 hover:text-blue-600 hover:bg-gray-50 rounded-md transition-colors"
                >
                  Analytics
                </a>
                <a
                  href={`/${params.businessId}/settings`}
                  className="block px-3 py-2 text-base font-medium text-gray-700 hover:text-blue-600 hover:bg-gray-50 rounded-md transition-colors"
                >
                  Settings
                </a>
              </div>
            </div>
          </header>

          {/* Usage Warning Banner */}
          {(businessConfig.subscription.currentUsage.orders / businessConfig.subscription.ordersLimit > 0.8 ||
            businessConfig.subscription.currentUsage.messages / businessConfig.subscription.messagesLimit > 0.8) && (
            <div className="bg-yellow-50 border-l-4 border-yellow-400 p-4">
              <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
                <div className="flex">
                  <div className="flex-shrink-0">
                    <svg className="h-5 w-5 text-yellow-400" viewBox="0 0 20 20" fill="currentColor">
                      <path fillRule="evenodd" d="M8.257 3.099c.765-1.36 2.722-1.36 3.486 0l5.58 9.92c.75 1.334-.213 2.98-1.742 2.98H4.42c-1.53 0-2.493-1.646-1.743-2.98l5.58-9.92zM11 13a1 1 0 11-2 0 1 1 0 012 0zm-1-8a1 1 0 00-1 1v3a1 1 0 002 0V6a1 1 0 00-1-1z" clipRule="evenodd" />
                    </svg>
                  </div>
                  <div className="ml-3">
                    <p className="text-sm text-yellow-700">
                      <strong>Usage Alert:</strong> You're approaching your plan limits. 
                      Orders: {businessConfig.subscription.currentUsage.orders}/{businessConfig.subscription.ordersLimit}, 
                      Messages: {businessConfig.subscription.currentUsage.messages}/{businessConfig.subscription.messagesLimit}
                      {' '}
                      <a href="/pricing" className="font-medium underline hover:text-yellow-800">
                        Upgrade your plan
                      </a>
                    </p>
                  </div>
                </div>
              </div>
            </div>
          )}

          {/* Main Content */}
          <main className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
            {children}
          </main>

          {/* Footer */}
          <footer className="bg-white border-t mt-12">
            <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-6">
              <div className="flex justify-between items-center">
                <div className="text-sm text-gray-500">
                  ¬© 2024 {businessConfig.name}. Powered by WhatsApp Order Manager.
                </div>
                <div className="flex space-x-6 text-sm text-gray-500">
                  <a href="/docs" className="hover:text-gray-700 transition-colors">Documentation</a>
                  <a href="/support" className="hover:text-gray-700 transition-colors">Support</a>
                  <a href="/pricing" className="hover:text-gray-700 transition-colors">Pricing</a>
                </div>
              </div>
            </div>
          </footer>
        </div>

        {/* Custom CSS for dynamic theming */}
        <style jsx global>{`
          :root {
            --primary-color: ${businessConfig.primaryColor};
            --secondary-color: ${businessConfig.secondaryColor};
          }
          
          .btn-primary {
            background-color: var(--primary-color);
            border-color: var(--primary-color);
          }
          
          .btn-primary:hover {
            background-color: color-mix(in srgb, var(--primary-color) 90%, black);
            border-color: color-mix(in srgb, var(--primary-color) 90%, black);
          }
          
          .text-primary {
            color: var(--primary-color);
          }
          
          .border-primary {
            border-color: var(--primary-color);
          }
          
          .bg-primary {
            background-color: var(--primary-color);
          }
        `}</style>
      </body>
    </html>
  );
}
