export interface BusinessConfig {
  id: string;
  name: string;
  logo?: string;
  primaryColor: string;
  secondaryColor: string;
  googleSheetUrl: string;
  whatsappTemplates: {
    confirmation: string;
    shipping: string;
    delivery: string;
    followUp: string;
  };
  contactInfo: {
    phone: string;
    email: string;
    address?: string;
  };
  subscription: {
    plan: 'starter' | 'professional' | 'enterprise';
    ordersLimit: number;
    messagesLimit: number;
    currentUsage: {
      orders: number;
      messages: number;
    };
  };
  settings: {
    autoConfirmation: boolean;
    includeOrderNumber: boolean;
    customDomain?: string;
  };
}

