export function getBusinessConfig(businessId: string): BusinessConfig | null {
  return demoBusinesses[businessId] || null;
}

export function validateBusinessId(businessId: string): boolean {
  return businessId in demoBusinesses;
}

export function getAllBusinesses(): BusinessConfig[] {
  return Object.values(demoBusinesses);
}

export function updateBusinessConfig(businessId: string, updates: Partial<BusinessConfig>): BusinessConfig | null {
  const business = demoBusinesses[businessId];
  if (!business) return null;
  
  demoBusinesses[businessId] = { ...business, ...updates };
  return demoBusinesses[businessId];
}

// Template variable replacement utility
export function replaceTemplateVariables(
  template: string,
  variables: {
    customerName: string;
    product: string;
    businessName: string;
    orderNumber?: string;
    phone?: string;
  }
): string {
  let message = template;
  
  message = message.replace(/{customerName}/g, variables.customerName);
  message = message.replace(/{product}/g, variables.product);
  message = message.replace(/{businessName}/g, variables.businessName);
  
  if (variables.orderNumber) {
    message = message.replace(/{orderNumber}/g, variables.orderNumber);
  }
  
  return message;
}

// Usage tracking utilities
export function trackOrderProcessed(businessId: string): void {
  const business = demoBusinesses[businessId];
  if (business) {
    business.subscription.currentUsage.orders += 1;
  }
}

export function trackMessageSent(businessId: string): void {
  const business = demoBusinesses[businessId];
  if (business) {
    business.subscription.currentUsage.messages += 1;
  }
}

export function checkUsageLimits(businessId: string): {
  ordersExceeded: boolean;
  messagesExceeded: boolean;
  ordersRemaining: number;
  messagesRemaining: number;
} {
  const business = demoBusinesses[businessId];
  if (!business) {
    return {
      ordersExceeded: true,
      messagesExceeded: true,
      ordersRemaining: 0,
      messagesRemaining: 0
    };
  }
  
  const ordersRemaining = business.subscription.ordersLimit - business.subscription.currentUsage.orders;
  const messagesRemaining = business.subscription.messagesLimit - business.subscription.currentUsage.messages;
  
  return {
    ordersExceeded: ordersRemaining <= 0,
    messagesExceeded: messagesRemaining <= 0,
    ordersRemaining: Math.max(0, ordersRemaining),
    messagesRemaining: Math.max(0, messagesRemaining)
  };
}
